


























































































































import { Component, Prop, Vue } from "vue-property-decorator";
@Component
export default class Website extends Vue {
  private container: Element | null = null;
  private title = "Backup380";

  public scroll() {
    this.container = this.$el.querySelector("div");
    if (this.container != null) {
      window.scrollTo({ top: this.container.scrollHeight, behavior: "smooth" });
    }
  }
  private downloadLinux() {
    const anchor = document.createElement("a");
    anchor.href = window.location.href + "/Backup380SetupLinux.sh";
    anchor.target = "_blank";
    anchor.download = "Backup380SetupLinux.sh";
    anchor.click();
  }
  private downloadMac() {
    const anchor = document.createElement("a");
    anchor.href = window.location.href + "/Backup380SetupMac.sh";
    anchor.target = "_blank";
    anchor.download = "Backup380SetupMac.sh";
    anchor.click();
  }
}
